<template>
  <div>
    <!--<div class="logo"></div>-->
    <div class="app-sidebar">
      <el-row class="tac">
        <el-col :span="24">
          <el-menu
            :collapse="isCollapse"
            class="el-menu-vertical-demo app-sidebar-li"
            @open="handleOpen"
            @close="handleClose"
            @select="selectMenu"
          >
            <template v-for="(item, index) of menus">
              <template v-if="item.children && item.children.length">
                <el-submenu :index="item.url" :key="index">
                  <template slot="title">
                    <i :class="item.ico"></i>
                    <span class="first-menu">{{item.label}}</span>
                  </template>
                  <el-menu-item-group>
                    <el-menu-item v-for="(subItem, index) of item.children" :key="index" :index="subItem.url" >
                      <i :class="subItem.ico"></i>
                      <span class="second-menu">{{subItem.label}}</span>
                    </el-menu-item>
                  </el-menu-item-group>
                </el-submenu>
              </template>
              <template v-else>
                <el-menu-item :key="index" :index="item.url">
                  <i :class="item.ico"></i>
                  <span slot="title" class="first-menu">{{item.label}}</span>
                </el-menu-item>
              </template>
            </template>
            <!--<template v-for="(item, index) of menus">
              <template v-if="!item.children || !item.children.length">
                <el-menu-item :key="index" :index="item.url">
                  <i :class="item.ico"></i>
                  <span slot="title" class="first-menu">{{item.label}}</span>
                </el-menu-item>
              </template>
            </template>-->
          </el-menu>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { menus } from '@/statics/menus'; 
export default {
  data() {
    return {
      isCollapse: false,
      menus: menus,
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      //console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      //console.log(key, keyPath);
    },
    selectMenu(urlPath) {
      //console.log('当前的url', urlPath);
      this.$router.push({name:urlPath});
    }
  }
};
</script>

<style lang="scss">
.sidebar{
  position: relative;
  .logo{
    height: 150px;
    text-align: center;
    a{
      text-decoration: none;
      color: #fff;
    }
  }
  .app-sidebar{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color:#293846 !important;
  }
}
.app-sidebar-li,
.app-sidebar-li .el-menu-item,
.app-sidebar-li .el-menu-item-group__title{
  background-color:#293846;
}
.first-menu{
  color:#FFF;
}
.second-menu{
  color:#7d8898;
}
.app-sidebar-li .el-submenu__title:hover,
.app-sidebar-li .el-menu-item:hover{
  span{
    color:#FFF;
  }
  background-color: #293846;
}
.app-sidebar-li .el-menu-item.is-active{
  span{
    color:#FFF;
  }
}
</style>


