<template>
  <div class="app-header">
    <div class="logo">
      <!--<router-link to="/">主页</router-link>-->
    </div>
    <div class="header-info">
      统一配置平台
    </div>
    <div class="userinfo-submenu">
      <el-dropdown>
        <span class="el-dropdown-link">
          <span>{{username}}</span>
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <router-link to="/modifyPwd"><el-dropdown-item>修改密码</el-dropdown-item></router-link>
          <!--<el-dropdown-item>个人资料</el-dropdown-item>-->
          <el-dropdown-item divided @click.native="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import { authToken, authName } from '@/config';
import { storageHelper } from '@/utils';
export default {
  data() {
    return {
      username: '',
    }
  },
  methods: {
    logout() {
      storageHelper.removeItem(authToken, 'healthcare');
      this.$router.push({name: 'login'});
    }
  },
  mounted() {
    this.username = storageHelper.getItem(authName, 'healthcare');
  }
};
</script>

<style lang="scss" scoped>
.app-header{
  height: 80px;
  /*min-width:1550px;*/
  background: #393d49;
  display: flex;
  flex-direction: row;
  line-height: 80px;
  color: #fff;
  .logo{
    width: 150px;
    height: inherit;
    text-align: center;
    a{
      text-decoration: none;
      color: #fff;
    }
  }
  .header-info {
    flex: 1;
    height: 80px;
    margin: 0 10px;
    line-height: 80px;
  }
  .userinfo-submenu{
    width: 150px;
    height: inherit;
    text-align: right;
    margin-right: 2rem;
    .el-dropdown{
      color: #fff;
    }
  }
}
a{
  text-decoration:none;
}
</style>


