const routers = [{
  name: 'cooperation',
  path: '/cooperation',
  meta: { title: '合作伙伴信息' },
  component: () => import('@/views/ecologyCooperation'),
  redirect: '/cooperation',
  children: [
    {
      name: 'ecologyCooperation.cooperation',
      path: '/cooperation',
      meta: { title: '合作伙伴信息' },
      component: () => import('@/views/ecologyCooperation/cooperation'),
    },
    {
      name: 'ecologyCooperation.contact',
      path: '/contact',
      meta: { title: '联系我们信息' },
      component: () => import('@/views/ecologyCooperation/contact'),
    },
  ],
}];

export default routers;



