  left: 0;
  top: 80px;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: row;
  .sidebar{
    width: 250px;
    height: inherit;
    background: #fff;
    padding: 1rem 0;
    box-shadow: 0 0 1.2em rgba(0,0,0,.12);
