import LoginServer from '@/server/login';
export default {
  state: {},
  mutations: {},
  actions: {
    // 登陆
    async loginApi({ commit }, params) {
      try {
        const { code, message, data } = await LoginServer.login(params);
        return new Promise((resolve, reject) => {
          if (code == 0) {
            resolve({data});
          } else {
            reject(message);
          }
        })
      } catch (e) {
        console.error(e);
      }
    },
  }
}



