<template>
  <div>
    <script id="editor" type="text/plain"></script>
  </div>
</template>

<script>
import { authToken,authName } from '@/config';
import { storageHelper,getCurTime,utcTimeChange } from '@/utils';
export default {
  name: "ueditor",
  data() {
    return {
      editor: null
    };
  },
  props: {
    defaultMsg: {
      type: String
    },
    config: {
      type: Object
    }
  },
  mounted() {
    const _this = this;
    this.editor = UE.getEditor("editor", this.config); // 初始化UE
    this.editor.addListener("ready", function() {
      _this.editor.setContent(_this.defaultMsg); // 确保UE加载完成后，放入内容。
    });
    this.editor.addListener('blur',function(editor){
      //console.log(_this.editor.getContent());
      _this.$emit("postEditorToParent",_this.editor.getContent());
    });
  },
  methods: {
    getUEContent() {
      // 获取内容方法
      return this.editor.getContent();
    }
  },
  destroyed() {
    this.editor.destroy();
  }
};
</script>

<style scoped>
</style>



