import login from './shared/login';
import home from './shared/home';
import demo2 from './demo2';
import demo1 from './demo1';
import demo3 from './demo3';
import contentManage from './contentManage';
import ecologyCooperation from './ecologyCooperation';
import newProjectManage from './newProjectManage';
import pageManage from './pageManage';
import projectManage from './projectManage';
import systemManage from './systemManage';

const router = [
  ...login,
  {
    path: '/',
    name: 'contentManage.news',
    meta: { title: '新闻管理' },
    component: () => import('@/components/framement/'),
    redirect: '/news',
    children: [
      ...home,
      ...demo1,
      ...demo2,
      ...demo3,
      ...contentManage,
      ...ecologyCooperation,
      ...newProjectManage,
      ...pageManage,
      ...projectManage,
      ...systemManage,
    ],
  },
];

export default router;



