const routers = [{
  name: 'foot',
  path: '/foot',
  meta: { title: 'foot管理' },
  component: () => import('@/views/pageManage'),
  redirect: '/foot',
  children: [
    {
      name: 'pageManage.foot',
      path: '/foot',
      meta: { title: 'foot管理' },
      component: () => import('@/views/pageManage/foot'),
    },
    {
      name: 'pageManage.menu',
      path: '/menu',
      meta: { title: '导航栏管理' },
      component: () => import('@/views/pageManage/menu'),
    },
    {
      name: 'pageManage.component',
      path: '/component',
      meta: { title: '组件管理' },
      component: () => import('@/views/pageManage/component'),
    },
    {
      name: 'pageManage.paper',
      path: '/paper',
      meta: { title: '页面管理' },
      component: () => import('@/views/pageManage/paper'),
    },
    {
      name: 'pageManage.statisPaper',
      path: '/statisPaper',
      meta: { title: '静态页面管理' },
      component: () => import('@/views/pageManage/statisPaper'),
    },
  ],
}];

export default routers;



