const routers = [{
  name: 'systemManage',
  path: '/systemManage',
  meta: { title: '系统管理' },
  component: () => import('@/views/systemManage'),
  redirect: '/users',
  children: [
    {
      name: 'systemManage.users',
      path: '/users',
      meta: { title: '用户管理' },
      component: () => import('@/views/systemManage/users'),
    },
    {
      name: 'systemManage.modifyPwd',
      path: '/modifyPwd',
      meta: { title: '修改密码' },
      component: () => import('@/views/systemManage/modifyPwd'),
    }
  ],
}];

export default routers;



