const routers = [{
  name: 'contentManage',
  path: '/contentManage',
  meta: { title: '内容管理' },
  component: () => import('@/views/contentManage'),
  redirect: '/news',
  children: [
    {
      name: 'contentManage.homeBanner',
      path: '/homeBanner',
      meta: { title: 'PC端轮播图' },
      component: () => import('@/views/contentManage/homeBanner'),
    },
    {
      name: 'contentManage.h5HomeBanner',
      path: '/h5HomeBanner',
      meta: { title: '移动端轮播图' },
      component: () => import('@/views/contentManage/h5HomeBanner'),
    },
    {
      name: 'contentManage.solution',
      path: '/solution',
      meta: { title: '解决方案管理' },
      component: () => import('@/views/contentManage/solution'),
    },
    {
      name: 'contentManage.notice',
      path: '/notice',
      meta: { title: '公告管理' },
      component: () => import('@/views/contentManage/notice'),
    },
    {
      name: 'contentManage.news',
      path: '/news',
      meta: { title: '新闻管理' },
      component: () => import('@/views/contentManage/news'),
      children: [
        {
          name: 'contentManage.news.newsDetails',
          path: '/newsDetails',
          meta: { title: '合作案例管理' },
          component: () => import('@/views/contentManage/news/newsDetails'),
        },
      ]
    },
    {
      name: 'contentManage.cases',
      path: '/cases',
      meta: { title: '合作案例管理' },
      component: () => import('@/views/contentManage/cases'),
    },
    {
      name: 'contentManage.tabs',
      path: '/tabs',
      meta: { title: '焦点TAB管理' },
      component: () => import('@/views/contentManage/tabs'),
    },
    {
      name: 'contentManage.advantage',
      path: '/advantage',
      meta: { title: '优势管理' },
      component: () => import('@/views/contentManage/advantage'),
    },
    {
      name: 'contentManage.publicService',
      path: '/publicService',
      meta: { title: '公共服务' },
      component: () => import('@/views/contentManage/publicService'),
    },
    {
      name: 'contentManage.product',
      path: '/product',
      meta: { title: '产品管理' },
      component: () => import('@/views/contentManage/product'),
    },
    {
      name: 'contentManage.data',
      path: '/data',
      meta: { title: '数据管理' },
      component: () => import('@/views/contentManage/data'),
    },
    {
      name: 'contentManage.counsel',
      path: '/counsel',
      meta: { title: '咨询管理' },
      component: () => import('@/views/contentManage/counsel'),
    },
  ],
}];

export default routers;



